body {
  --selected-color: #00acdc;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

header {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.logoLink {
  align-items: center;
  display: flex;
}

.logoLink .logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav .navList {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav .navList .navListItem {
  padding-left: 20px;
  list-style-type: none;
}

.nav .navList .navListItem:first-of-type {
  padding: 0;
}

.nav .navList .navListItem .navListItemLink {
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  padding: 0;
  text-decoration: none;
  display: flex;
}

.nav .navList .navListItem .navListItemLink:not(:hover) {
  color: #000;
}

.nav .navList .navListItem .navListItemLink:hover, .nav .navList .navListItem .navListItemLink.is-active {
  color: var(--selected-color);
}

.nav .navList .navListItem .navListItemLink.is-active:after {
  background: var(--selected-color);
  content: "";
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  top: 56px;
}

